
.form-group div.selectize-input {
  min-height: auto;
  padding: 4px 12px;
  border-radius: 0px;
}
.form-group .selectize-control.single .selectize-input .item,
.form-group .selectize-control.multi .selectize-input .item {
  background-color: #eeeeee;
  padding: 1px 7px;
  border-radius: 3px;
}
.form-group.hidden {
  display: none;
}



